.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__text-wrap {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  @media(min-width: @width-tablet) {
    width: 50%;
  }
}

.header__img-wrap {
  width: 100%;

  @media(min-width: @width-tablet) {
    width: 50%;
  }

  @media(min-width: @width-desktop) {
    width: 472px;
    height: 217px;
  }
}