.about {
  display: flex;
  flex-wrap: wrap;
}

.about__text-wrap {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  @media(min-width: @width-tablet) {
    width: 50%;
  }

}

.about__video-wrap {
  position: relative;
  padding: 0 0 56.25% 0;
  height: 0;
  overflow: hidden;

  // @media(min-width: @width-tablet) {
  //   width: 50%;
  // }

  // @media(min-width: @width-desktop) {
  //   width: 430px;
  //   height: 275px;
  // }
}

.about__video-wrap iframe,
.about__video-wrap object,
.about__video-wrap embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}